.fr-view table{border:0;border-collapse:collapse;empty-cells:show;max-width:100%}

.fr-view table td{min-width:5px}

.fr-view table.fr-dashed-borders td,.fr-view table.fr-dashed-borders th{border-style:dashed}

.fr-view table.fr-alternate-rows tbody tr:nth-child(2n){background:#f5f5f5}

.fr-view table td,.fr-view table th{border:1px solid #ddd}

.fr-view table td:empty,.fr-view table th:empty{height:20px}

.fr-view table td.fr-highlighted,.fr-view table th.fr-highlighted{border:1px double red}

.fr-view table td.fr-thick,.fr-view table th.fr-thick{border-width:2px}

.fr-view table th{background:#e6e6e6}

.fr-view hr{clear:both;user-select:none;-o-user-select:none;-moz-user-select:none;-khtml-user-select:none;-webkit-user-select:none;-ms-user-select:none;page-break-after:always}

.fr-view .fr-file{position:relative}

.fr-view .fr-file::after{position:relative;content:"\1F4CE";font-weight:400}

.fr-view pre{white-space:pre-wrap;word-wrap:break-word;overflow:visible}

.fr-view[dir=rtl] blockquote{border-left:0;border-right:solid 2px #5e35b1;margin-right:0;padding-right:5px;padding-left:0}

.fr-view[dir=rtl] blockquote blockquote{border-color:#00bcd4}

.fr-view[dir=rtl] blockquote blockquote blockquote{border-color:#43a047}

.fr-view blockquote{border-left:solid 2px #5e35b1;margin-left:0;padding-left:5px;color:#5e35b1}

.fr-view blockquote blockquote{border-color:#00bcd4;color:#00bcd4}

.fr-view blockquote blockquote blockquote{border-color:#43a047;color:#43a047}

.fr-view span.fr-emoticon{font-weight:400;font-family:"Apple Color Emoji","Segoe UI Emoji",NotoColorEmoji,"Segoe UI Symbol","Android Emoji",EmojiSymbols;display:inline;line-height:0}

.fr-view span.fr-emoticon.fr-emoticon-img{background-repeat:no-repeat!important;font-size:inherit;height:1em;width:1em;min-height:20px;min-width:20px;display:inline-block;margin:-.1em .1em .1em;line-height:1;vertical-align:middle}

.fr-view .fr-text-gray{color:#AAA!important}

.fr-view .fr-text-bordered{border-top:solid 1px #222;border-bottom:solid 1px #222;padding:10px 0}

.fr-view .fr-text-spaced{letter-spacing:1px}

.fr-view .fr-text-uppercase{text-transform:uppercase}

.fr-view .fr-class-highlighted{background-color:#ff0}

.fr-view .fr-class-code{border-color:#ccc;border-radius:2px;-moz-border-radius:2px;-webkit-border-radius:2px;background-clip:padding-box;background:#f5f5f5;padding:10px;font-family:"Courier New",Courier,monospace}

.fr-view .fr-class-transparency{opacity:.5}

.fr-view img{position:relative;max-width:100%}

.fr-view img.fr-dib{margin:5px auto;display:block;float:none;vertical-align:top}

.fr-view img.fr-dib.fr-fil{margin-left:0;text-align:left}

.fr-view img.fr-dib.fr-fir{margin-right:0;text-align:right}

.fr-view img.fr-dii{display:inline-block;float:none;vertical-align:bottom;margin-left:5px;margin-right:5px;max-width:calc(100% - (2 * 5px))}

.fr-view img.fr-dii.fr-fil{float:left;margin:5px 5px 5px 0;max-width:calc(100% - 5px)}

.fr-view img.fr-dii.fr-fir{float:right;margin:5px 0 5px 5px;max-width:calc(100% - 5px)}

.fr-view span.fr-img-caption{position:relative;max-width:100%}

.fr-view span.fr-img-caption.fr-dib{margin:5px auto;display:block;float:none;vertical-align:top}

.fr-view span.fr-img-caption.fr-dib.fr-fil{margin-left:0;text-align:left}

.fr-view span.fr-img-caption.fr-dib.fr-fir{margin-right:0;text-align:right}

.fr-view span.fr-img-caption.fr-dii{display:inline-block;float:none;vertical-align:bottom;margin-left:5px;margin-right:5px;max-width:calc(100% - (2 * 5px))}

.fr-view span.fr-img-caption.fr-dii.fr-fil{float:left;margin:5px 5px 5px 0;max-width:calc(100% - 5px)}

.fr-view span.fr-img-caption.fr-dii.fr-fir{float:right;margin:5px 0 5px 5px;max-width:calc(100% - 5px)}

.fr-view .fr-video{text-align:center;position:relative}

.fr-view .fr-video.fr-rv{padding-bottom:56.25%;padding-top:30px;height:0;overflow:hidden}

.fr-view .fr-video.fr-rv>iframe,.fr-view .fr-video.fr-rv object,.fr-view .fr-video.fr-rv embed{position:absolute!important;top:0;left:0;width:100%;height:100%}

.fr-view .fr-video>*{-webkit-box-sizing:content-box;box-sizing:content-box;max-width:100%;border:0}

.fr-view .fr-video.fr-dvb{display:block;clear:both}

.fr-view .fr-video.fr-dvb.fr-fvl{text-align:left}

.fr-view .fr-video.fr-dvb.fr-fvr{text-align:right}

.fr-view .fr-video.fr-dvi{display:inline-block}

.fr-view .fr-video.fr-dvi.fr-fvl{float:left}

.fr-view .fr-video.fr-dvi.fr-fvr{float:right}

.fr-view a.fr-strong{font-weight:700}

.fr-view a.fr-green{color:green}

.fr-view .fr-img-caption{text-align:center}

.fr-view .fr-img-caption .fr-img-wrap{padding:0;display:inline-block;margin:auto;text-align:center;width:100%}

.fr-view .fr-img-caption .fr-img-wrap img{display:block;margin:auto;width:100%}

.fr-view .fr-img-caption .fr-img-wrap>span{margin:auto;display:block;padding:5px 5px 10px;font-size:14px;font-weight:initial;-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-opacity:.9;-moz-opacity:.9;opacity:.9;-ms-filter:"alpha(Opacity=0)";width:100%;text-align:center}

.fr-view button.fr-rounded,.fr-view input.fr-rounded,.fr-view textarea.fr-rounded{border-radius:10px;-moz-border-radius:10px;-webkit-border-radius:10px;background-clip:padding-box}

.fr-view button.fr-large,.fr-view input.fr-large,.fr-view textarea.fr-large{font-size:24px}

